import registerServiceWorker from './registerServiceWorker';
import App from './app';

import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';

ReactDOM.render((
	<HashRouter>
		<App />
	</HashRouter>
), document.getElementById('root'));

registerServiceWorker();


